<template>
  <div id="PhotoMemory" class="pb-16">
    
    <Alert ref="Alert"/>

    <InteractiveBord @dataForApp="updateTimeOver" @changeSetting="changeSettings()" :count="Tcount" :timeLimit="timeLimit" ref="InteractiveBord"/>



    <div class="w-full flex justify-center">
      <div class="w-full flex flex-wrap max-w-5xl">
        <div class="md:p-1 w-1/6 h-20 sm:h-24 md:h-32" v-for="(item, index) in photos" :key="index">

          <div class="flip-box rounded-lg overflow-hidden cursor-pointer " @click="addfilp(item)">
            <div :class="item.isFilp ? 'flip-now':''" class="flip-box-inner">
              <div class="flip-box-front">
                <img class="w-full h-full object-center" src="../assets/img/logo.png" alt="musab">
              </div>
              <div class="flip-box-back">
                <img class="w-full h-full object-center" :src="item.imgPath" alt="musab">
              </div>
            </div>
          </div>

        </div>
      </div>
    </div>

    <LevelsSelect @dataForApp="updateLevelValue" />
  
  </div>
</template>

<script>
import Alert from '../components/Alert'
import InteractiveBord from '../components/InteractiveBord'
import LevelsSelect from '../components/LevelsSelect'
export default {
  name: 'PhotoMemory',
  components: {
    Alert,
    InteractiveBord,
    LevelsSelect
  },
  data() {
    return {
      hasfilp:false,
      first:null,
      scound:null,

      count:0,

      timeLimit:0,

      stopGame:false,

      level:0,

      photos:[]
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      //this.addImg();
      this.randomAddImg();

      this.viewAllImg();
      this.addTimeLimit();
    },
    addfilp(obj){
      var vm=this;
      if(vm.first != null && vm.scound != null)return;
      if(obj.isFilp == true)return;
      if(vm.stopGame)return;
      if(!vm.$refs.InteractiveBord.timerInterval) vm.$refs.InteractiveBord.startTimer();
      obj.isFilp = true
      if (!vm.hasfilp) {
        vm.first = obj
        vm.hasfilp = true;
      }else{
        vm.scound = obj
        vm.hasfilp = false;
        vm.check();
      }
    },
    check(){
      var vm = this;
      if (vm.first.id == vm.scound.id) {
        console.log(vm.first.id +":"+ vm.scound.id)
        vm.count++;
        this.$refs.Alert.alertShow("+1");
        this.ifAllDone();
        vm.first = null;
        vm.scound = null;
      }else{
        setTimeout(function() {
          vm.first.isFilp = false;
          vm.scound.isFilp = false;
          vm.first = null;
          vm.scound = null;
        }, 1000);
      }
    },
    ifAllDone(){
      var done = true;
      this.photos.forEach(item => {
        if(item.isFilp != true){
          done = false;
          return false;
        }
      });
      if(done) {
        this.$refs.Alert.alertShow("You Win :)","bg-green-500",10000);
        this.$refs.InteractiveBord.stopTimer();
        console.log("allFilpDone");
      }
    },
    addImg(){
      var n;
      if(this.level == 0)n=18;
      else if(this.level == 1)n=24;
      else if(this.level == 2)n=30;
      for (let i = 0; i < n; i++) {
        this.photos.push({
          id: Math.floor(i/2),
          imgPath:require("../assets/img/"+Math.floor(i/2)+".jpg"),
          isFilp:false
        });
      }
    },
    randomAddImg(){
      var n;
      if(this.level == 0)n=18;
      else if(this.level == 1)n=24;
      else if(this.level == 2)n=30;
      for(var a=[],i=0;i<n;i++)a[i]=i;

      var temp , current , top = a.length;
      if(top) while (--top) {
        current = Math.floor(Math.random() * (top + 1));
        temp = a[current]
        a[current] = a[top];
        a[top] = temp;
      }
      
      for (let n = 0; n < a.length; n++) {
        this.photos.push({
          id: Math.floor(a[n]/2),
          imgPath:require("../assets/img/"+Math.floor(a[n]/2)+".jpg"),
          isFilp:false
        });
      }
    },
    addTimeLimit(){
      var time = 0;
      if(this.level == 0)time=60;
      else if(this.level == 1)time=80;
      else if(this.level == 2)time=100;

      this.timeLimit = time;
    },
    viewAllImg(){
      var time = 0;
      if(this.level == 0)time=3000;
      else if(this.level == 1)time=4000;
      else if(this.level == 2)time=5000;
      var vm=this;
      vm.photos.forEach(item => {
        item.isFilp = true;
      });
      clearTimeout(this.viewAllImgTimer)
      this.viewAllImgTimer = setTimeout(function() {
        vm.photos.forEach(item => {
          item.isFilp = false;
        });
      }, time);
    },

    updateTimeOver(){
      this.stopGame = true;
      if(this.first)this.first.isFilp = false;
      this.$refs.Alert.alertShow("Game Over :(","bg-red-500",10000);
    },
    updateLevelValue(value){
      this.level = value;
      this.changeSettings();
    },

    changeSettings(){
      var vm = this;
      var level = vm.level;
      vm.$refs.InteractiveBord.stopTimer(1);
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.MStart();
    }
  },
  computed: {
    Tcount(){
      return this.count +'/'+ this.photos.length/2
    }
  },
}
</script>

<style>
.flip-box{
  background-color: transparent;
  width: 100%;
  height: 100%;
  border: 2px solid #f1f1f1;
  perspective: 1000px;
}

.flip-box-inner{
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 1.5s;
  transform-style: preserve-3d;
}

.flip-now{
  transform: rotateY(180deg);
}

.flip-box-front, .flip-box-back{
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-box-front{
  background-color: #bbb;
  color: black;
}

.flip-box-back{
  background-color: #555;
  color: white;
  transform: rotateY(180deg);
}
</style>
